import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { NgSelectModule } from '@ng-select/ng-select';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { PackingComponent } from './packing.component';
import { PackingService } from 'src/app/service/packing.service';

const routes: Routes = [
  {
    path: '',
    component: PackingComponent,
    data: { title: 'packing_list' },
  },
  {
    path: 'add',
    loadChildren: () => import('./add-packing/add-packing.module').then(m => m.AddPackingModule),
    data: { title: 'add_packing' },
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('./add-packing/add-packing.module').then(m => m.AddPackingModule),
    data: { title: 'edit_packing' },
  },
  {
    path: 'view/:id',
    loadChildren: () => import('./packing-view/packing-view.module').then(m => m.PackingViewModule),
    data: { title: 'view_packing' },
  },


]


@NgModule({
  declarations: [PackingComponent],
  imports: [
    CommonModule,
    FormsModule,
    NgSelectModule,
    RouterModule.forChild(routes),
    SearchModule,
    BtnAddModule,
    PaginationModule,
    TableListModule,
  ], providers: [PackingService]
})
export class PackingModule { }
